@import '../../styles/variables';

.main {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
}

.header {
    position: relative;
    z-index: 2;
    h1 {
        margin: 10px 0;
        font-size: clamp(1rem, 3vw, 4rem);
        color: $main-color;
    }
}

.logo {
    position: relative;
    top: -2%;

    width: 55%;
    height: 50%;

    @media (max-width: 950px) {
        width: 95%;
        top: 0;
    }
}

